import styled from 'styled-components';

export const ListContainer = styled.div`
  background-color: #eee;
  padding: 12px;
  margin: 16px 0 12px 0;
  width: 300px;
  min-height: 100px;
  border-radius: 8px;
  transition: 100ms ease-in-out;
  overflow: hidden;
  overflow-anchor: none;
  position: relative;
`;
